  },
  mounted() {
      this.myimgCode = 'https://app.coinsaja.com/app/reg/getCode?use='+this.getPicType +'&v=' + Math.random();
      this.contracyNames();
      this.myLangeCode();
      this.myChaneLange();
      this.referralCode = this.$route.query.extensionCode;
  },
  methods: {
        goBack(){
            this.popupLange = false;
            this.popupVisible = false;
            this.popupContry = false;
        },
        changLang(name,value){
            this.popupLange = false;
            this.langeName = name;
            this.langCode = value;
            this.myLangeCode();
        },
        myChaneLange(index){
            if(index == 1){
                this.popupLange = true;
            }
            this.$ajax({
                method:'get',
                url:'https://app.coinsaja.com/app/config/getSysLangDicInfo'
            }).then(res=>{
                if(res.data.success == true){
                    console.log(res.data.obj);
                    this.langeList = JSON.parse(res.data.obj);
                    for(var i=0; i<this.langeList.length; i++){
                        if(this.langCode == this.langeList[i].value){
                            this.langeName = this.langeList[i].name;
                        }
                    }
                }
            })
        },
        myChaneContry(index){
            if(index == 1){
                this.popupContry = true;
                console.log(this.allCountriesData);
            }
        },
        myLangeCode(){
            this.$ajax({
                method:'get',
                url:'https://app.coinsaja.com/app/config/findWordsByLang',
                data:{
                    sourceType:this.sourceType,
                    langCode:this.langCode,
                    "locale":this.langCode,
                }
            }).then(res=>{
                console.log(res.data);
                if(res.data.success == true){
                    this.zhuce = res.data.obj.zhuce;
                    this.shoujizhuce = res.data.obj.shoujizhuce;
                    this.youxianzhuce = res.data.obj.youxianzhuce;
                    this.shurshoujhm = res.data.obj.shurshoujhm;
                    this.zhengquesjhm = res.data.obj.zhengquesjhm;
                    this.emailisnull = res.data.obj.emailisnull;
                    this.emailvail = res.data.obj.emailvail;
                    this.tishi = res.data.obj.tishi;
                    this.sure = res.data.obj.sure;
                    this.please_write_email = res.data.obj.please_write_email;
                    this.duanxinyanzma = res.data.obj.duanxinyanzma;
                    this.newpwd_no_null = res.data.obj.newpwd_no_null;
                    this.passvail = res.data.obj.passvail;
                    this.twopwd_is_diff = res.data.obj.twopwd_is_diff;
                    this.huoquyanzm = res.data.obj.huoquyanzm;
                    this.jiaoyitishi2 = res.data.obj.mimageshi2;
                    this.querenmima = res.data.obj.querenmima;
                    this.imageCodeisnull = res.data.obj.imageCodeisnull;
                    this.tuijianrenshoujihao = res.data.obj.tuijianrenshoujihao;
                    this.yuedujieshou = res.data.obj.yuedujieshou;
                    this.fuwuxieyi = res.data.obj.fuwuxieyi;
                    this.szyuyin = res.data.obj.szyuyin;
                }
            })
        },
        contracyNames(){
            for (let i = 0; i < this.countryicon.length; i++) {
                let c = this.countryicon[i];
                let item = {
                    text: c[0],
                    value:c[1]+'_'+c[2],
                    countryNum:"+"+c[2],
                };
                this.allCountriesData.push(item);
            }
            for(let i=0; i<this.allCountriesData.length; i++){
                if(this.countryNumber == this.allCountriesData[i].text){
                    this.countryNum = this.allCountriesData[i].countryNum;
                }
            }
            console.log(this.allCountriesData);
        },
        toggleTab(tab) {
            this.currentTab = tab;
            this.emailer = '';
            this.phoneNumber = '';
        },
        //小图标点击显示隐藏
        toggleSH() {
            this.cur = !this.cur;
        },
        //国家选中切换  并传递国家区号
        select(index){
            console.log(this.allCountriesData[index])
            this.countryNumber = this.allCountriesData[index].text;
            this.countryNum = this.allCountriesData[index].countryNum;
            this.popupContry = false;
        },
        myImgCode(){
            this.myimgCode = 'https://app.coinsaja.com/app/reg/getCode?use='+this.getPicType +'&v=' + Math.random();
        },
        geCode(){
            if(this.currentTab == 'phonezh'){
                if(this.phoneNumber == ''){
                    MessageBox({
                        title: this.tishi,
                        message: this.shurshoujhm,
                        confirmButtonText:this.sure
                    });
                    return;
                }else if(!this.INPUT.telNumberVerification(this.phoneNumber)){
                    MessageBox({
                        title: this.tishi,
                        message: this.zhengquesjhm,
                        confirmButtonText:this.sure
                    });
                    return;
                }else if(this.imgCode == ''){
                    MessageBox({
                        title: this.tishi,
                        message: this.imageCodeisnull,
                        confirmButtonText:this.sure
                    });
                    return;
                }
            }else if(this.currentTab == 'emailzh'){
                if(this.emailer == ''){
                    MessageBox({
                        title: this.tishi,
                        message: this.emailisnull,
                        confirmButtonText:this.sure
                    });
                    return;
                }else if(!this.INPUT.emailVerification(this.emailer)){
                    MessageBox({
                        title: this.tishi,
                        message: this.emailvail,
                        confirmButtonText:this.sure
                    });
                    return;
                }
            }
            if (!this.timer) {
                this.count = 120;
                this.show = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= 120) {
                        this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000)
            }
            if(this.currentTab == 'phonezh'){
                this.$ajax({
                    method:'post',
                    url:'https://app.coinsaja.com/app/sms/registSmsCodeByImgCode',
                    data:{
                        country: this.countryNum,
                        imgCode: this.imgCode,
                        langCode:this.langCode,
                        locale:this.langCode,
                        mobile: this.phoneNumber,
                    }
                }).then(res=>{
                    console.log(res);
                    if(res.data.success == true){
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                    }else{
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                        return;
                    }
                })
            }else if(this.currentTab == 'emailzh'){
                this.$ajax({
                    method:'post',
                    url:'https://app.coinsaja.com/app/reg/regSendEmail',
                    data:{
                        "email": this.emailer,
                        'langCode':this.langCode,
                        locale:this.langCode,
                    }
                }).then(res=>{
                    console.log(res.data);
                    if(res.data.success == true){
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                    }else{
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                        return;
                    }
                })
            }
        },
        myRegAgree(){
            if(this.popupVisible == false){
                this.popupVisible = true;
            }else{
                this.popupVisible = false;
            }
            this.$ajax({
                method:'post',
                url:'https://app.coinsaja.com/app/reg/initRegPageData',
                data:{
                    'langCode':this.langCode,
                    locale:this.langCode,
                }
            }).then(res=>{
                console.log(res.data);
                this.regreg = res.data.regreg;
            })
        },
        myRegBtn(){
            if(this.currentTab == 'phonezh'){
                if(this.phoneNumber == ''){
                    MessageBox({
                        title: this.tishi,
                        message: this.shurshoujhm,
                        confirmButtonText:this.sure
                    });
                    return;
                }else if(!this.INPUT.telNumberVerification(this.phoneNumber)){
                    MessageBox({
                        title: this.tishi,
                        message: this.zhengquesjhm,
                        confirmButtonText:this.sure
                    });
                    return;
                }
            }else if(this.currentTab == 'emailzh'){
                if(this.emailer == ''){
                    MessageBox({
                        title: this.tishi,
                        message: this.emailisnull,
                        confirmButtonText:this.sure
                    });
                    return;
                }else if(!this.INPUT.emailVerification(this.emailer)){
                    MessageBox({
                        title: this.tishi,
                        message: this.emailvail,
                        confirmButtonText:this.sure
                    });
                    return;
                }
            }
            if(this.registSmsCode == ''){
                MessageBox({
                    title: this.tishi,
                    message: this.duanxinyanzma,
                    confirmButtonText:this.sure
                });
                return;
            }else if(this.password == ''){
                MessageBox({
                    title: this.tishi,
                    message: this.newpwd_no_null,
                    confirmButtonText:this.sure
                });
                return;
            }else if(!this.INPUT.passwordVerification(this.password)){
                MessageBox({
                    title: this.tishi,
                    message: this.passvail,
                    confirmButtonText:this.sure
                });
                return;
            }else if(this.password != this.opassword){
                MessageBox({
                    title: this.tishi,
                    message: this.twopwd_is_diff,
                    confirmButtonText:this.sure
                });
                return;
            }else if(this.imgCode == ''){
                MessageBox({
                    title: this.tishi,
                    message: this.imageCodeisnull,
                    confirmButtonText:this.sure
                });
                return;
            }
            var password = this.hrymd5.md5(this.password);
            if(this.currentTab == 'phonezh'){
                this.$ajax({
                    method:'post',
                    url:'https://app.coinsaja.com/app/reg/mobileRegistByImgCode',
                    data:{
                        "mobile": this.phoneNumber,//手机号
                        "country": this.countryNum,
                        "imgCode": this.imgCode,//图形验证码
                        'langCode':this.langCode,
                        locale:this.langCode,
                        "password":password,
                        "referralCode":this.referralCode,//推荐吗
                        "registSmsCode":this.registSmsCode//短信验证码
                    }
                }).then(res=>{
                    console.log(res.data);
                    if(res.data.success == true){
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                    }else{
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                        return;
                    }
                })
            }else if(this.currentTab == 'emailzh'){
                this.$ajax({
                    method:'post',
                    url:'https://app.coinsaja.com/app/reg/emailRegistByImgCode',
                    data:{
                        "email": this.emailer,//邮箱
                        "imgCode": this.imgCode,//图形验证码
                        'langCode':this.langCode,
                        locale:this.langCode,
                        "password":password,
                        "referralCode":this.referralCode,//推荐吗
                        "registObjCode":this.registSmsCode//邮箱验证码
                    }
                }).then(res=>{
                    console.log(res.data);
                    if(res.data.success == true){
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                        return;
                    }else{
                        MessageBox({
                            title: this.tishi,
                            message: res.data.msg,
                            confirmButtonText:this.sure
                        });
                        return;
                    }
                })
            }
        },
  },
  computed:{
  },
}
</script>

