<style scoped>
.flag-dropdown {
    width: 30%;
    left: 134px;
    top: 4px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    float: left;
}
.selected-flag .arrow{
    top:16px;
}
.selected-flag .flag{
    top:12px;
}
.country-name{
    width:82% !important;
    text-align:left !important;
}
.country-list{
    top:37px;
}
.dail-txt{
    line-height: 28px;
}
.country-list {
    list-style: none;
    position: absolute;
    z-index: 2;
    padding: 0;
    margin: 21px 0 0 -1px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    background-color: white;
    border: 1px solid #ccc;
    width: 100%;
    max-height: 200px;
    overflow-y: scroll;
    text-align: left;
}
.country-list li {
    position: relative;
    line-height: 1;
    padding: 7px 10px 7px 10px;
    cursor: pointer;
    font-size: 12px;
}
.country-list li:hover {
    background-color: rgba(0,0,0,0.05);
}
</style>



